<template>
	<view class="content">
		<view class="phone">
			<input maxlength="11" type="number" placeholder="请输输入手机号" style="padding-left: 15px;" />
		</view>
		<view class="ident">
			<input maxlength="6" type="number" placeholder="请输验证码" style="padding-left: 15px;" />
			<input class="code" @click="getVerification" v-model="code" />
		</view>
		<view class="id">
			<input maxlength="16" type="text" placeholder="请输入用户名" style="padding-left: 15px;" />
		</view>
		<view class="pwd">
			<input type="password" maxlength="16" :password="paw" placeholder="请输入新密码" style="padding-left: 15px;" />
		</view>
		<view class="mkpwd">
			<input type="password" maxlength="16" :password="paw" placeholder="请确定新密码" style="padding-left: 15px;" />
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				paw: "",
				code: "获取验证码"
			}
		},
		methods: {
			getVerification() {
				if (this.code != "获取验证码") {
					uni.showToast({
						title: '请稍后再试',
						icon: "none"
					})
				} else {
					this.code = 60;
					var jian = setInterval(() => {
						this.code--
						if (this.code == 0) {
							clearInterval(jian)
							this.code = "获取验证码"
						}
					}, 1000)
				}
			}
		}
	}
</script>

<style lang="scss">
	.content {
		top: 20rpx;
		position: relative;
		display: flex;
		width: 650rpx;
		height: 800rpx;
		justify-content: space-between;
		flex-direction: column;
		padding-top: 10px;
		margin: 50rpx;

		.phone {
			position: relative;
			display: flex;
			border: 1px solid #C8C7CC;
			height: 80rpx;
			align-items: center;
			border-radius: 20px;
		}

		.ident {
			position: relative;
			display: flex;
			border: 1px solid #C8C7CC;
			height: 80rpx;
			align-items: center;
			border-radius: 20px;

			.code {
				flex: 3;
				font-size: 25rpx;
				text-align: center;
				float: right;
			}
		}
		.id {
			position: relative;
			display: flex;
			border: 1px solid #C8C7CC;
			height: 80rpx;
			align-items: center;
			border-radius: 20px;
		}
		.pwd {
			position: relative;
			display: flex;
			border: 1px solid #C8C7CC;
			height: 80rpx;
			align-items: center;
			border-radius: 20px;
		}
		
		.mkpwd {
			position: relative;
			display: flex;
			border: 1px solid #C8C7CC;
			height: 80rpx;
			align-items: center;
			border-radius: 20px;
		}

	}
</style>
